@import url('https://fonts.googleapis.com/css?family=Ubuntu:300,400,400i,500,500i,700&amp;subset=cyrillic');

@mixin font-reg-norm() {
  font-family: 'Ubuntu', sans-serif;
  font-weight: 400;
  font-style: normal; }

@mixin font-lig-norm() {
  font-family: 'Ubuntu', sans-serif;
  font-weight: 300;
  font-style: normal; }

@mixin font-reg-ital() {
  font-family: 'Ubuntu', sans-serif;
  font-weight: 400;
  font-style: italic; }

@mixin font-med-norm() {
  font-family: 'Ubuntu', sans-serif;
  font-weight: 500;
  font-style: normal; }

@mixin font-med-ital() {
  font-family: 'Ubuntu', sans-serif;
  font-weight: 500;
  font-style: italic; }

@mixin font-bold-norm() {
  font-family: 'Ubuntu', sans-serif;
  font-weight: 700;
  font-style: normal; }

$gold: #fdd716;

@mixin text-prop($align) {
  color: #444444;
  @if $align == 'right' {
    margin-right: 95px;
    text-align: right;
    margin-left: 25px; }
  @if $align == 'left' {
    margin-left: 90px;
    text-align: left;
    margin-right: 25px; }
  h3 {
    font-size: 16px;
    @include font-bold-norm();
    text-transform: uppercase;
    padding-bottom: 15px; }
  p {
    @include font-lig-norm();
    line-height: 1.2em; } }

@mixin background ($path,$width,$height) {
  background-image: $path;
  background-size: contain;
  background-repeat: no-repeat;
  width: $width;
  height: $height; }

